import { createRouter, createWebHistory } from "vue-router";

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes:[
        {
            path: '/',
            redirect: '/home',
        },
        {
            path: '/home',
            name: 'Home',
            component: () => import('../components/AngerHome.vue')
        },
        {
            path: '/homeplay',
            name: 'HomePlay',
            component: () => import('../components/AngerPlay.vue')
        },
        {
            path: '/anger',
            name: 'Ira',
            component: () => import('../components/AngerIra.vue')
        },
        {
            path: '/angerpulse',
            name: 'Angerpulse',
            component: () => import('../components/AngerWhat.vue')
        },
        {
            path: '/angerwho',
            name: 'Para quien',
            component: () => import('../components/AngerWho.vue')
        },
        {
            path: '/Comousarlo',
            name: 'Comousarlo',
            component: () => import('../components/AngerHow.vue')
        },
        {
            path: '/cuandousarlo',
            name: 'Cuandousarlo',
            component: () => import('../components/AngerWhen.vue')
        },
        {
            path: '/info',
            name: 'Informacion',
            component: () => import('../components/AngerInfo.vue')
        },
        {
            path: '/configuracion',
            name: 'Configuracion',
            component: () => import('../components/AngerConfig.vue')
        },
        {
            path: '/audioteca',
            name: 'Audioteca',
            component: () => import('../components/AngerAudioteca.vue')
        },
        {
            path: '/dashboard',
            name: 'Dashboard',
            component: () => import('../components/AngerDashboard.vue')
        },
        {
            path: '/perfil',
            name: 'Perfil',
            component: () => import('../components/AngerUser.vue')
        },
        {
            path: '/plan',
            name: 'Plan',
            component: () => import('../components/AngerTrial.vue')
        }
    ],
    scrollBehavior(to, from, savedPosition) {
        if (to.hash) {
          return {
            el: to.hash,
            behavior: 'smooth', // Optional: Add smooth scrolling
          };
        } else if (savedPosition) {
          return savedPosition;
        } else {
          return { top: 0 };
        }
      },
})

export default router
